<style></style>
<script>
    import { ref } from "vue";

    const columns = [
    { 
    name: 'id', 
    label: '#', 
    field: 'id', 
    sortOrder: 'ad',
    sortable: true,
    },
    {
        name: "desc",
        align: "left",
        label: "Nome",
        field: "name",
        required: true,
        sortable: true,

        
    },
    { name: 'price_single', align: 'right', label: 'Único', field: 'price_single' },
    { name: 'price_small', align: 'left', label: '-10kg', field: 'price_small' },
    { name: 'price_medium', align: 'left', label: '+10kg', field: 'price_medium' },
    { name: 'price_large', align: 'left', label: '+20kg', field: 'price_large' },
    { name: 'price_internal', align: 'left', label: 'Interno', field: 'price_internal' },
    { name: 'price_external', align: 'right', label: 'Externo', field: 'price_external' }

    ];

    const item = [
    {
        id: 0,
        name: "Novo Item",
        price_small: 0.00,
        price_medium: 0.00,
        price_large: 0.00,
        status: 1
    },
    ];
    export default {
    name: "TablePrice",
    props: {
        title: String,
        apiurl: String,
        visible_columns: Array,
        cid: Number
    },
    setup() {
        const rows = ref([]);
        const rowCount = ref(10);
        const loading = ref(false);

        rows.value.reverse();

        return {
        searchPlaceholder: ref("Pesquisar"),
        price_small: 0,
        price_medium: 0,
        price_large: 0,
        selected: ref([]),
        sortBy: "id",
        sortOrder: 'da',
        filter: ref(""),
        leftDrawerOpen: ref(false),
        rows,
        item: ref(item),
        rowCount,
        columns,
        loading,
        async addRow() {

            const index = rows.value.length + 1;

            const row = item[0];
            //row.id = index;
            
            const newRow = { ...row }; // extend({}, row, { name: `${row.name} (${row.__count})` })

            

            rows.value = [
                ...rows.value.slice(0, index),
                newRow,
                ...rows.value.slice(index),
            ];         

            //rows.value.reverse();

            const id = newRow.id
            console.log(id)
            
        },
        
        };
    },

    methods: {
        labelDeletar() {
        return "Clique no botão deletar para excluir esse item.";
        },
        async getServicos() {

        this.loading = true;

        const req = await fetch(this.apiurl);
        this.rows = await req.json();
        this.rows = this.rows.data;

        this.loading = false;


        },
        async updateApi(id, key, value, scope) {

            this.loading = true

            const array = [];
            array['id'] = id;
            array['cid'] = this.cid;
            array['key'] = key;
            array[key] = value;    
            array['status'] = 'active';  


        const dataJson = JSON.stringify(Object.assign({}, array));        

            const req = await fetch(this.apiurl, {
                method: "POST",
                mode: 'cors',
                headers: {
                    'Origin': 'clientes.test',
                    'Accept': 'application/json, text/javascript, */*; q=0.01',
                    'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
                }, 
                body: dataJson,
            });
            const res = await req.json();
            console.log(res);
            scope.set();
            if(key === 'name'){
                this.getServicos();
                
            }
            this.loading = false
        },
        async deleteApi(array) {

        const dataJson = JSON.stringify({ id: array[0].id, status: "deleted" });

        const req = await fetch(this.apiurl, {
            method: "POST",
            mode: 'cors',
            headers: {
                'Origin': 'clientes.test',
                'Accept': 'application/json, text/javascript, */*; q=0.01',
                'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
            }, 
            body: dataJson,
        });

        const res = await req.json();
        this.selected = ''
        console.log(res);
        this.getServicos();
        },
        priceFormat(price){
        price = price ? parseInt(price) : 0
        return price.toLocaleString('pt-br',{style: 'currency', currency: 'BRL'})
        },
        getPaginationLabel(){
            
        }
    },
    mounted() {
        this.getServicos();
    },
    computed:{

    },
    created() {
       
    },

    //
    };
</script>